import React from 'react';
import { motion } from 'framer-motion';
import '../styles/MotivationalQuote.css';

//   
interface Quote {
  text: string;
  author: string;
}

interface QuotesDatabase {
  [category: string]: Quote[];
}

//     
export const motivationalQuotes: QuotesDatabase = {
  personal: [
    { text: "  ,      .", author: " " },
    { text: "       ,   .", author: " " },
    { text: "     ,    .", author: " " },
  ],
  work: [
    { text: "    ,     ,   .", author: " " },
    { text: "      .      .", author: " " },
    { text: "    ,         .", author: " " },
  ],
  education: [
    { text: "      ;     .", author: " " },
    { text: "      .", author: " " },
    { text: ",   ,  ,     .", author: "" },
  ],
  health: [
    { text: "'     ,    .", author: "" },
    { text: "    .     ,  .", author: "... " },
    { text: "   ' -    .", author: "" },
  ],
  financial: [
    { text: "   ,         .", author: " " },
    { text: "    ,   ,   .", author: " " },
    { text: "    ,      .", author: " " },
  ],
  family: [
    { text: "'     .  .", author: " . " },
    { text: "     .", author: " " },
    { text: " '    ,   '  -.", author: " " },
  ],
  creative: [
    { text: "    .", author: " " },
    { text: "        .", author: " " },
    { text: "    .     ,      .", author: " " },
  ],
  spiritual: [
    { text: "   .         .", author: " " },
    { text: "   ,    .   ,    .", author: "'   " },
    { text: "        .", author: "" },
  ],
};

interface MotivationalQuoteProps {
  category: string;
}

const MotivationalQuote: React.FC<MotivationalQuoteProps> = ({ category }) => {
  //      
  const getRandomQuote = (category: string): Quote => {
    //        ,   
    if (!motivationalQuotes[category] || motivationalQuotes[category].length === 0) {
      return {
        text: "       .",
        author: "-"
      };
    }
    
    const quotes = motivationalQuotes[category];
    const randomIndex = Math.floor(Math.random() * quotes.length);
    return quotes[randomIndex];
  };

  const quote = getRandomQuote(category);

  return (
    <motion.div 
      className="motivational-quote"
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <div className="quote-icon"></div>
      <blockquote>{quote.text}</blockquote>
      <cite> {quote.author}</cite>
    </motion.div>
  );
};

export default MotivationalQuote; 